import React, {
  ChangeEvent,
  forwardRef,
  memo,
  ReactElement,
  Ref,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState
} from 'react';
import { createPortal } from 'react-dom';
import cn from 'classnames';
import { CommonSelectOption } from '@thyme-ui/types';
import { useFloating, size as floatingSize, autoUpdate, flip as floatingFlip } from '@floating-ui/react-dom';

import { Label } from '../Label';
import { Chip } from '../Chip';

import type { SelectProps } from './Select.types';
import chevronUp from './icons/chevron-up';
import chevronDown from './icons/chevron-down';
import xMark from './icons/x-mark';
import magnifier from './icons/magnifier';

// @TODO: move later
enum EKeyboardCodes {
  ARROW_UP = 'ArrowUp',
  ARROW_DOWN = 'ArrowDown',
  ENTER = 'Enter',
  ESCAPE = 'Escape',
  SPACE = 'Space',
  TAB = 'Tab'
}

const popoverRootDefault = document.getElementsByTagName('body')[0];

const RCN = 'thuiSelect';

const SelectComponent = <T extends CommonSelectOption = CommonSelectOption>(
  {
    id,
    name,
    options,
    defaultSelectedValue,
    value,
    label,
    placeholder,
    searchPlaceholder = 'Search...',
    disabled,
    multiple,
    renderOption,
    renderTag,
    emptyOptionsPlaceholder = <div className={`${RCN}__empty-options`}>No options</div>,
    emptySearchPlaceholder = <div className={`${RCN}__empty-options`}>No results found</div>,
    // if user doesn't pass custom icons use default
    arrowDownIcon = chevronDown,
    arrowUpIcon = chevronUp,
    clearIcon = xMark,
    searchIcon = magnifier,
    removeTagIcon = xMark,
    size = 'md',
    optionsListSize = 'full',
    isClearable = false,
    isSearchable = false,
    searchFunc,
    isInvalid,
    isRequired,
    isInitiallyExpanded = false,
    error,
    isExpanded,
    onExpand,
    onCollapse,
    onSelect,
    onSearch,
    onClear,
    popoverRoot = popoverRootDefault,
    placement = 'bottom-start',
    fitViewPort,
    flip
  }: SelectProps<T>,
  ref: Ref<HTMLDivElement>
) => {
  const selectCnString = cn(RCN, `${RCN}--${size}`, {
    [`${RCN}--invalid`]: isInvalid,
    [`${RCN}--disabled`]: disabled,
    [`${RCN}--required`]: isRequired
  });

  const selectRef = useRef<HTMLButtonElement>(null);
  const optionsListRef = useRef<HTMLDivElement | null>(null);

  const isExpandControlled = isExpanded !== undefined && onExpand !== undefined && onCollapse !== undefined;

  const [localIsExpanded, setLocalIsExpanded] = useState(isInitiallyExpanded);

  const expanded = useMemo(
    () => (isExpandControlled ? isExpanded : localIsExpanded),
    [isExpandControlled, isExpanded, localIsExpanded]
  ); 

  const { refs, floatingStyles } = useFloating({
    open: expanded,
    strategy: 'fixed',
    placement,
    whileElementsMounted: (referenceEl, floatingEl, update) => {
      return autoUpdate(referenceEl, floatingEl, update, {
        // Prevents shifting during anchor transition
        layoutShift: false,
      });
    },
    middleware: [
      flip && floatingFlip(),
      optionsListSize === 'full' && floatingSize({
        apply({ rects, elements }) {
          Object.assign(elements.floating.style, {
            width: `${rects.reference.width}px`
          });
        }
      }),
      fitViewPort && floatingSize({
        apply({ availableHeight, elements }) {
          elements.floating.style.maxHeight = `${availableHeight}px`;
        }
      })
    ],
  });

  const [localValue, setLocalValue] = useState<T['value'] | T['value'][] | undefined>(
    defaultSelectedValue ?? undefined
  );

  const currentValue = useMemo(() => value ?? localValue, [localValue, value]);

  const [activeOption, setActiveOption] = useState<T['value'] | null>(null);

  const clearActiveOption = useCallback(() => setActiveOption(null), []);

  const [showClearIcon, setShowClearIcon] = useState(false);

  const isDirty = useMemo(
    () => (Array.isArray(currentValue) ? currentValue.length > 0 : !!currentValue),
    [currentValue]
  );

  const isSearchControlled = onSearch !== undefined;
  const [localSearch, setLocalSearch] = useState('');

  const handleExpand = useCallback(() => {
    if (isExpandControlled) {
      onExpand?.();
    } else {
      setLocalIsExpanded(true);
    }

    if (selectRef.current) selectRef.current.ariaExpanded = 'true';
    if (currentValue && !Array.isArray(currentValue)) setActiveOption(currentValue);
  }, [isExpandControlled, currentValue, onExpand]);

  const handleCollapse = useCallback(() => {
    if (isExpandControlled) {
      onCollapse?.();
    } else {
      setLocalIsExpanded(false);
    }

    if (selectRef.current) selectRef.current.ariaExpanded = 'false';
    if (activeOption) clearActiveOption();
    if (localSearch.length > 0) setLocalSearch('');
  }, [isExpandControlled, activeOption, clearActiveOption, localSearch.length, onCollapse]);

  const toggleExpanded = useCallback(
    (event: MouseEvent | React.MouseEvent) => {
      event.stopPropagation();
      if (expanded) {
        handleCollapse();
      } else {
        handleExpand();
      }
    },
    [expanded, handleCollapse, handleExpand]
  );

  const handleSelect = useCallback(
    (optionValue: T['value']) => () => {
      if (onSelect) {
        onSelect(optionValue);
      } else {
        multiple ? setLocalValue([...(currentValue as string[]), optionValue]) : setLocalValue(optionValue);
      }

      if (!multiple) {
        handleCollapse();
      }
    },
    [currentValue, handleCollapse, multiple, onSelect]
  );

  const handleClear = useCallback(() => {
    if (isDirty) {
      if (onClear) {
        onClear();
      } else {
        setLocalValue(undefined);
      }
    }
  }, [isDirty, onClear]);

  const handleArrowClick = useCallback(
    (event: MouseEvent | React.MouseEvent) => {
      if (isClearable && isDirty) {
        handleClear();
      } else {
        toggleExpanded(event);
      }
    },
    [handleClear, isClearable, isDirty, toggleExpanded]
  );

  const handleSearchChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      event.stopPropagation();

      const searchValue = event.target.value;

      if (isSearchControlled) onSearch(searchValue);
      else setLocalSearch(searchValue);
    },
    [isSearchControlled, onSearch]
  );

  const handleClickOutside = useCallback(
    (event: MouseEvent) => {
      if (
        optionsListRef.current &&
        !optionsListRef.current.contains(event.target as Node) &&
        !selectRef.current?.contains(event.target as Node)
      ) {
        if (expanded) handleCollapse();
      }
    },
    [expanded, handleCollapse]
  );

  const handleKeydown = useCallback(
    (event: KeyboardEvent) => {
      const select = selectRef.current;
      const code = event.code as EKeyboardCodes;
      const openKeys = [EKeyboardCodes.ARROW_UP, EKeyboardCodes.ARROW_DOWN, EKeyboardCodes.ENTER, EKeyboardCodes.SPACE];
      const navigateKeys = [EKeyboardCodes.ARROW_UP, EKeyboardCodes.ARROW_DOWN];
      const closeKeys = [EKeyboardCodes.ESCAPE, EKeyboardCodes.TAB];
      const selectKeys = [EKeyboardCodes.ENTER, EKeyboardCodes.SPACE];

      // EXPAND WHEN FOCUSED
      if (document.activeElement === select && !expanded && openKeys.includes(code)) {
        event.preventDefault();
        handleExpand();
      } else if (expanded) {
        // NAVIGATE THROUGH OPTIONS
        if (navigateKeys.includes(code)) {
          event.preventDefault();
          const activeOptionIndex = options.findIndex((option) => option.value === activeOption);

          if (code === EKeyboardCodes.ARROW_UP) {
            const nextOption = activeOption ? options[activeOptionIndex - 1] : options[options.length - 1];

            if (nextOption) setActiveOption(nextOption.value);
          }

          if (code === EKeyboardCodes.ARROW_DOWN) {
            const nextOption = activeOption ? options[activeOptionIndex + 1] : options[0];

            if (nextOption) setActiveOption(nextOption.value);
          }
        }

        // COLLAPSE
        if (closeKeys.includes(code)) {
          handleCollapse();
        }

        // SELECT ON SPACE/ENTER
        if (selectKeys.includes(code) && activeOption) {
          if (code === EKeyboardCodes.SPACE) {
            event.preventDefault();
          }

          if (Array.isArray(currentValue) || (!Array.isArray(currentValue) && activeOption !== currentValue)) {
            handleSelect(activeOption)();
          } else {
            handleCollapse();
          }
        }
      }
    },
    [expanded, handleExpand, activeOption, options, handleCollapse, currentValue, handleSelect]
  );

  const displayOptionsList = useMemo(() => {
    if (!options.length) {
      return emptyOptionsPlaceholder;
    }

    const optionsList =
      isSearchable && !isSearchControlled && localSearch.length > 0
        ? options?.filter((option) =>
            searchFunc
              ? searchFunc(localSearch, option)
              : String(option.value).toLowerCase()
                  .includes(localSearch.toLowerCase())
          )
        : options;

    if (!optionsList?.length) {
      return emptySearchPlaceholder;
    }

    return (
      <>
        {optionsList.map((option) => (
          <li
            role="option"
            className={cn({
              ['thuiOptions__option']: !renderOption,
              ['thuiOptions__option--active']: !renderOption && option.value === activeOption
            })}
            key={option.value}
            value={option.value}
            onMouseEnter={clearActiveOption}
            onClick={handleSelect(option.value)}
          >
            {renderOption ? renderOption(option, option.value === activeOption) : option.label}
          </li>
        ))}
      </>
    );
  }, [
    options,
    isSearchable,
    isSearchControlled,
    localSearch,
    emptyOptionsPlaceholder,
    searchFunc,
    emptySearchPlaceholder,
    renderOption,
    activeOption,
    clearActiveOption,
    handleSelect
  ]);

  const handleRemoveMultiple = useCallback(
    (optionValue: T['value']) => (event: MouseEvent | React.MouseEvent) => {
      event.stopPropagation();

      handleSelect(optionValue)();
    },
    [handleSelect]
  );

  const renderValue = useMemo(() => {
    if (Array.isArray(currentValue)) {
      return isDirty ? (
        <div className={`${RCN}__tags-wrapper`}>
          {currentValue?.map((optionValue) => {
            const optionToRender = options.find((option) => option.value === optionValue);
            if (!optionToRender) return null;

            return renderTag ? (
              renderTag(optionToRender)
            ) : (
              <Chip
                className={`${RCN}__tag`}
                key={`${name}_${optionToRender.value}`}
                title={optionToRender.label}
                size={size}
                iconRight={
                  <div className={`${RCN}__tag-remove`} onClick={handleRemoveMultiple(optionToRender.value)}>
                    {removeTagIcon}
                  </div>
                }
              ></Chip>
            );
          })}
        </div>
      ) : (
        <span className={`${RCN}__placeholder`}>{placeholder}</span>
      );
    } else {
      const optionToRender = options.find((option) => option.value === currentValue);

      if (!optionToRender) return <span className={`${RCN}__placeholder`}>{placeholder}</span>;

      return optionToRender?.label;
    }
  }, [currentValue, handleRemoveMultiple, isDirty, name, options, placeholder, removeTagIcon, renderTag, size]);

  const setOptionsRefHandler = useCallback((el: HTMLDivElement | null) => {
    optionsListRef.current = el;
    refs.setFloating(el);
  }, [refs]);

  useEffect(() => {
    document.addEventListener('click', handleClickOutside);
    document.addEventListener('keydown', handleKeydown);

    return () => {
      document.removeEventListener('click', handleClickOutside);
      document.removeEventListener('keydown', handleKeydown);
    };
  }, [handleClickOutside, handleKeydown]);

  return (
    <div className={selectCnString} ref={ref}>
      <div className={`${RCN}__select-wrapper`} ref={refs.setReference}>
        <button
          ref={selectRef}
          id={id}
          name={name}
          className={`${RCN}__select`}
          disabled={disabled}
          onClick={toggleExpanded}
          type="button"
          role="combobox"
          aria-labelledby="select button"
          aria-haspopup="listbox"
          aria-expanded="false"
          aria-controls="select-dropdown"
        >
          {renderValue}
        </button>
        <div
          className={cn(`${RCN}__select-icon`, `${RCN}__select-icon--${size}`)}
          onClick={handleArrowClick}
          onMouseEnter={() => setShowClearIcon(true)}
          onMouseLeave={() => setShowClearIcon(false)}
        >
          {isClearable && showClearIcon && isDirty ? clearIcon : expanded ? arrowUpIcon : arrowDownIcon}
        </div>
      </div>
      {/* @TODO: separate thuiDropdown */}
      {expanded && createPortal(
        <div
          className={cn(
            'thuiDropdown',
            `thuiDropdown--${size}`
          )}
          style={{
            ...floatingStyles,
            ...(typeof optionsListSize === 'number' && { width: `${optionsListSize}px` })
          }}
          ref={setOptionsRefHandler}
        >
          {/* @TODO: separate thuiOptions */}
          <ul role="listbox" className="thuiOptions">
            {isSearchable && (
              <li className={cn(`thuiOptions__search`)}>
                <input
                  className={`thuiOptions__search-input`}
                  placeholder={searchPlaceholder}
                  onChange={handleSearchChange}
                  autoFocus
                />
                <div className={cn(`thuiOptions__search-icon`, `thuiOptions__search-icon--${size}`)}>{searchIcon}</div>
              </li>
            )}
            {displayOptionsList}
          </ul>
        </div>,
        popoverRoot
      )}

      {label && (
        <Label label={label} htmlFor={name} isRequired={isRequired} className={`${RCN}__label`} disabled={disabled} />
      )}
      {error && <div className={cn(`${RCN}__error`, `${RCN}__error--${size}`)}>{error}</div>}
    </div>
  );
};

export const Select = memo(forwardRef(SelectComponent)) as <T extends CommonSelectOption = CommonSelectOption>(
  props: SelectProps<T> & { ref?: Ref<HTMLDivElement> }
) => ReactElement;
